@font-face {
	font-family: 'Euclid Square';
	src: url('../fonts/EuclidSquare_Medium/EuclidSquare_Medium.eot');
	src: local('EuclidSquare_Medium'), local('../fonts/EuclidSquare_Medium/EuclidSquare_Medium'),
		url('../fonts/EuclidSquare_Medium/EuclidSquare_Medium.eot?#iefix') format('embedded-opentype'),
		url('../fonts/EuclidSquare_Medium/EuclidSquare_Medium.woff') format('woff'),
		url('../fonts/EuclidSquare_Medium/EuclidSquare_Medium.ttf') format('truetype');
	font-weight: 500;
	font-style: normal;
	font-display: swap;
}

